
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapMutations ,mapActions} from "vuex";
export default {
name:"login",
components:{
Group,
XInput,
XButton,
Toast
},
data() {
return {
accesstoken:'',
isShowToast:false,
toastText:""
}
},
methods: {
...mapMutations(['mutationsLogined','mutationsLoginname','mutationsAccessToken','mutationsAuthorId']),
...mapActions(['getMsgsCount']),
doLogin(){
var app = this;
this.$http.post("/accesstoken",{
accesstoken:this.accesstoken
}).then(function(res){
console.log(res.data);
if(res && res.data.success){
app.mutationsLogined(); 
app.mutationsLoginname(res.data.loginname);
app.mutationsAccessToken(app.accesstoken);
app.mutationsAuthorId(res.data.id);  
app.getMsgsCount()


window.localStorage.setItem("loginStatus",true);
window.localStorage.setItem('loginname',res.data.loginname);
window.localStorage.setItem('accesstoken',app.accesstoken);
window.localStorage.setItem("author_id",res.data.id);



app.$router.push("/user/"+res.data.loginname);

}
}).catch(function(error){ //捕获错误信息
console.log(error.response)
app.isShowToast = true;
app.toastText = error.response.data.error_msg
})
}
},
}



