import Vue from 'vue'
import VueRouter from 'vue-router';
import Home from "../components/Home.vue";
import TopicList from "../components/TopicList.vue";
import Topic from "../components/Topic.vue";
import Login from "../components/Login.vue";
import Center from "../components/Center.vue";
import Msgs from "../components/Msgs.vue";
import Logout from "../components/Logout.vue";
import User from "../components/User.vue";
import Create from "../components/Create.vue";
import Edit from "../components/Edit.vue";

Vue.use(VueRouter)

const routes=[
  // {path:"/",component:Home},
  {path:"/",redirect:"/topiclist/all"},
  {path:"/topiclist/:name",name:"topiclist",component:TopicList,props:true}, //主题列表
  {path:"/topic/:id",name:"topic",component:Topic,props:true},  //主题详情
  {path:"/login",name:"login",component:Login},  //登录
  {path:"/center",name:"center",component:Center},  // 个人中心
  {path:"/msgs",name:"msgs",component:Msgs},  //消息中心
  {path:"/logout",name:"logout",component:Logout},  //退出
  {path:"/user/:loginname",name:"user",component:User,props:true}, // 用户中心
  {path:"/create",name:"create",component:Create},   //发表话题
  {path:"/topic/:id/edit",name:"edit",component:Edit,props:true} //编辑话题
];

const router = new VueRouter({
  routes
});
export default router;


